.input:focus {
  outline: none;
  border: #99ff99 solid 2px;
}

.superInput {
  margin: 10px;
  background: #003300;
  color: #99ff99;
}

.errorInput {
  margin: 10px;
  background: #003300;
  color: #99ff99;
  border: 2px solid #dd3355;
}

.error {
  color: #dd3355;
  height: 21px;
  margin-left: 10px;
}
